button {
  background: none;
  border: 2px solid #fff;
  padding: 6px 12px;
  border-radius: 4px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
}
button:hover {
  background: #c23866;
  color: #fff;
}
.card-grid {
  margin-top: 40px;
  display: grid;
  grid-template-columns: repeat(5, 19%);
  grid-gap: 15px;
}

.card-record {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-top: 20px;
  border: 1px solid #fff;
  font-weight: 600;
}
.show {
  visibility: visible;
}

.hidden {
  visibility: hidden;
}